<template>
  <div class="content">
    <div class="md-layout">
      <div class="md-layout-item md-size-15">
        <md-button class="md-success" @click="showDialog=true">Thm</md-button>
      </div>
      <div class="md-layout-item md-size-30">
        <md-field>
          <label>Tm kim</label>
          <md-input v-model="searchContent.textSearch" @change="searchInputChange"></md-input>
        </md-field>
      </div>
      <div class="md-layout-item md-size-20">
         <md-field>
          <label for="phanloai">Phn loi</label>
          <md-select v-model="searchContent.phanloai" >
            <md-option value="plate_number">Bin s xe</md-option>
            <md-option value="imei_device">Imei thit b</md-option>      
          </md-select>
        </md-field>
      </div>
    </div>
    <div class="md-layout">
      <div class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100">
        <md-card>
          <md-card-header data-background-color="green">
            <h4 class="title">Danh sch thit b</h4>
            <p class="category">Bng hin th thng tin thit b</p>
          </md-card-header>
          <md-card-content>
            <simple-table
              @hideDialog="hideDialog"
              :showDialog.sync="showDialog"
              :searchContent.sync="searchContent"
              table-header-color="green"
            ></simple-table>
          </md-card-content>
        </md-card>
      </div>
    </div>
  </div>
</template>

<script>
import { SimpleTable } from "@/components";

export default {
  components: {
    SimpleTable
  },
  data() {
    return {
      showDialog: false,
      active: true,      
      searchContent : {textSearch : "",phanloai : ""}
    };
  },
  methods: {
    hideDialog(value) {
      this.showDialog = value;
    },
    searchInputChange() {      
      if(this.searchContent.phanloai === ""){
        this.$message({type:"warning",message:"Bn vui lng chn phn loi trc khi tm kim!"})
        this.searchContent.textSearch = ''
      }else{        
        this.$bus.$emit('searchUpdate',this.searchContent)
      }
    }
  }
};
</script>
