<template>
  <div class="content">
    <div class="md-layout">
      <div class="md-layout-item">
        <md-button class="md-success" @click="active=true">Thm</md-button>
      </div>
    </div>
    <div class="md-layout">
      <div class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100">
        <md-card>
          <md-card-header data-background-color="green">
            <h4 class="title">Danh sch ti x</h4>
            <p class="category">Bng hin th thng tin ti x</p>
          </md-card-header>
          <md-card-content>
            <md-table v-model="drivers">
              <md-table-row
                slot="md-table-row"
                slot-scope="{ item }"
                md-auto-select
                md-selectable="multiple"
              >
                <md-table-cell md-label="Tn ti x ">{{ item.dr_name }}</md-table-cell>
                <md-table-cell md-label="nh">
                  <img class="driver_avatar" :src="item.dr_avatar" />
                </md-table-cell>
                <md-table-cell md-label="Thao tc">
                  <md-button class="md-raised edit_btn">Sa</md-button>
                  <md-button class="md-raised delete_btn" @click="activeDeleteDialog=true;selectedRow=item">Xa</md-button>
                </md-table-cell>
              </md-table-row>
            </md-table>
          </md-card-content>
        </md-card>
      </div>
    </div>
     <md-dialog :md-active.sync="active">
      <md-dialog-title>Thm thng tin xe</md-dialog-title>
      <md-content>
        <div>
          <md-field>
            <label>Tn ti x</label>
            <md-input v-model="name"></md-input>
          </md-field>
        </div>
      </md-content>
      <md-dialog-actions>
        <md-button class="md-primary" @click="active=false">Hy</md-button>
        <md-button class="md-primary" @click="submit();active=false">Lu</md-button>
      </md-dialog-actions>
    </md-dialog>
     <md-dialog-confirm
      :md-active.sync="activeDeleteDialog"
      md-title="Xa?"
      md-content="Bn c chc chn mun xa hay khng?"
      md-confirm-text="ng "
      md-cancel-text="Hy"
      @md-confirm="onConfirmDelete"
    />
  </div>
</template>

<script>
import { getAllDriver,insertDriver,deleteDriver } from "../api/driver";
export default {
  name: "driverPage",
  components: {},
  data() {
    return {
      drivers: [],
      active : false,
      name : "",
      selectedRow : {},
      activeDeleteDialog: false
    };
  },
  methods:{
    onConfirmDelete(){
      deleteDriver(this.selectedRow).then(result=>{
        this.getList()
      })
    },
    getList(){
      this.drivers = []
      getAllDriver().then(result=>{
        this.drivers = result.data
      })
    },
    submit(){
      insertDriver({dr_name : this.name}).then((result)=>{
         if(result.status===200){
           this.$message({type:"success",message:"Thm thnh cng"})
         }
      }).then(()=>{
        this.getList()
      })
    }
  },
  mounted() {
    getAllDriver().then(result => {
      this.drivers = result.data;
    });
  }
};
</script>
<style lang="scss" scoped>
.driver_avatar {
  width: 100px !important;
}
.delete_btn {
  background: #ff5252 !important;
}
.edit_btn {
  background: #448aff !important;
}
</style>
